<template>
  <div class="new_address">
    <div class="new_header">
      <x-header :left-options="{showBack:true,backText:''}">管理收货地址 <span class="save">保存</span></x-header>
    </div>
    <div class="new_content">
      <group>
        <x-input title="收货人"  placeholder=""></x-input>
        <x-input title="联系电话"  placeholder=""></x-input>
        <x-address  title="所在地区"  placeholder="请选择地址" :list="addressData" @on-hide="logHide"></x-address>
        <x-textarea  placeholder="请填写详细地址"></x-textarea>
      </group>
      <group>
        <x-switch title="设为默认"></x-switch>
      </group>
    </div>
  </div>
</template>

<script>
  import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'

  export default {
    components: {
      ChinaAddressData,
      XAddress,
      XSwitch,
      XTextarea,
      XHeader,
      Cell,
      Group,
      XInput
    },
    data () {
      return {
        addressData: ChinaAddressData
      }
    },
    methods: {
      logHide (str) {
        console.log(this.addressData)
      }
    }
  }
</script>

<style rel="stylesheet/less" lang="less">
  @import "../../assets/css/newaddress.less";
</style>



